<!-- 管理员菜单 -->

<template>
    <div>
        <el-row class="tac">
            <el-col :span="4">
                <el-menu default-active="1" class="el-menu-vertical-demo">
                    <el-menu-item index="1" @click="goVideo()">
                        <i class="el-icon-menu"></i>
                        <span slot="title">管理视频</span>
                    </el-menu-item>
                    <el-menu-item index="2" @click="goVideoForm()">
                        <i class="el-icon-setting"></i>
                        <span slot="title">上传视频</span>
                    </el-menu-item>
                    <el-menu-item index="3" @click="goLesson()">
                        <i class="el-icon-setting"></i>
                        <span slot="title">管理课程</span>
                    </el-menu-item>
                    <el-menu-item index="4" @click="goBlog()">
                        <i class="el-icon-setting"></i>
                        <span slot="title">管理博客</span>
                    </el-menu-item>
                    <el-menu-item index="5" @click="goUser()">
                        <i class="el-icon-setting"></i>
                        <span slot="title">管理学员</span>
                    </el-menu-item>

                </el-menu>
            </el-col>
            <el-col :span="20">
                <router-view />
            </el-col>
        </el-row>
    </div>
</template>

<script>
const options = {
    data() {
        return {}
    },
    methods: {
        push(url) {
            this.$router.push(url);
        },
        goVideo() {
            this.push('/Video')
        },
        goBlog() {
            this.push('/Blog')
        },
        goUser() {
            this.push('/User')
        },
        goVideoForm() {
            this.push('/VideoForm')
        },
        goLesson() {
            this.push('/Lesson')
        }

    }
}
export default options;

</script>